/*! Основные стили */

/*=============================================
      ОБЩИЕ ПРАВИЛА
  ============================================= */

.clearfix:after::after {
  display: block;
  content: "";
  clear: both;
}

.wrapper {
  max-width: 1250px;
  margin: 0 auto;
  padding-left: 40px;
  padding-right: 40px;
}

html {
  font-size: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: "AbelRegular";
  font-weight: 400;
  font-size: 15px;
  line-height: 1.375;
}

p,
ul {
  margin: 0;
  padding: 0;
}

/*============================================= */

/*=============================================
        ШАПКА
============================================= */

.header {
  background-color: #17191e;
  padding-top: 43px;
  padding-bottom: 228px;
}

.header__top {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: space-between;
  align-items: center;
}

.header__top .header__logo {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
}

.header__burger {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  margin-right: 26px;
  margin-top: -8px;
}

/* Кнопка меню-гамбургер
------------------------------------------------*/

.burger {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  z-index: 100;
  padding: 0;
  border: none;
  background: none;
  background: #ffda00;
  cursor: pointer;
  outline: none;
}

.burger__line {
  width: 17px;
  height: 2px;
  margin-bottom: 4px;
  background: #17191e;
  transition: all ease 0.2s;
}

.burger__line:last-child {
  margin-bottom: 0;
}

.burger--active .burger__line:first-child {
  position: relative;
  top: 6px;
  transform: rotate(45deg);
  transition: all ease 0.4s;
}

.burger--active .burger__line:nth-child(2) {
  opacity: 0;
}

.burger--active .burger__line:last-child {
  position: relative;
  top: -6px;
  transform: rotate(-45deg);
  transition: all ease 0.4s;
}

/*----------------------------------------------*/

/*Скрытое меню
------------------------------------------------*/

.nav {
  display: none;
  max-width: 100%;
  text-align: right;
}

.nav__link {
  display: block;
  padding: 3px 16px;
  color: #ffda00;
  font-family: "AbelRegular";
  font-size: 1.25rem;
  text-transform: uppercase;
}

.nav .nav__link:hover {
  background-color: #4d4100;
  transition: all ease 0.5s;
}

.visible {
  display: flex;
  justify-content: center;
  margin-top: 25px;
}

/*----------------------------------------------*/

.header__title {
  margin-top: 190px;
  margin-bottom: 9px;
  padding-left: 5px;
  color: #ffdb00;
  font-size: 12vw;
  line-height: 1.1;
  text-transform: uppercase;
  letter-spacing: 19px;
  font-weight: 100;
}

.header__desc {
  margin-bottom: 40px;
  color: #ffffff;
  font-size: 15px;
  line-height: 1.67;
  letter-spacing: 1px;
}

.header__button {
  display: inline-block;
  margin-left: -2px;
  border: 2px solid #ffda00;
  padding: 25px 60px;
  border-radius: 30px;
  color: #ffffff;
  font-size: 17px;
  line-height: 0.88;
  text-transform: uppercase;
  letter-spacing: 1.26px;
}

.header__button:hover {
  background: #ffda00;
}

/*============================================= */

/*=============================================
        ОСНОВНОЙ КОНТЕНТ
============================================= */

/*---------------------------------------------
  Секция с цитатой
--------------------------------------------- */

.quote {
  padding: 87px 0 92px;
  font-size: 20px;
  background-color: #ffda00;
}

.quote__text {
  max-width: 964px;
  margin: 0 auto;
  color: #181a1f;
  font-size: inherit;
  line-height: 1.25;
  letter-spacing: 1.5px;
  text-align: center;
}

/*----------------------------------------------*/

/*---------------------------------------------
  Секция услуги
--------------------------------------------- */

.amenities {
  padding: 119px 0;
  text-align: center;
}

.amenities__items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: center;
}

.amenities__button {
  display: inline-block;
  margin: 0 auto;
  border: 2px solid #ffda00;
  padding: 25px 98px;
  border-radius: 30px;
  color: #181a1f;
  font-size: 17px;
  line-height: 1;
  text-transform: uppercase;
  letter-spacing: 1.26px;
}

.amenities__button:hover {
  background: #ffda00;
}

.itemAmen {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(33.33333% - 30px);
  text-align: left;
}

.itemAmen__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 77px;
  height: 77px;
  background-color: #ffda00;
}

.itemAmen__title {
  margin-top: 28px;
  margin-bottom: 30px;
  color: #ffdb00;
  font-size: 50px;
  font-weight: 200;
  line-height: 1.53;
  text-transform: uppercase;
  letter-spacing: 3.75px;
}

.itemAmen__desc {
  margin-bottom: 86px;
  color: #181a1f;
  font-size: 15px;
  line-height: 1.3;
  letter-spacing: 3.2px;
}

/*----------------------------------------------*/

/*---------------------------------------------
  Секция  с показателями
--------------------------------------------- */

.indicators {
  padding: 95px 0 60px 0;
  background-color: #17191e;
}

.indicators__items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.itemIndic {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(25% - 30px);
  text-align: center;
}

.itemIndic__number {
  margin-top: 32px;
  color: #ffffff;
  font-size: 60px;
}

.itemIndic__title {
  margin-top: 5px;
  margin-bottom: 0;
  color: #ffdb00;
  font-size: 20px;
  font-weight: 400;
  line-height: 3.82;
  text-transform: uppercase;
  letter-spacing: 1.5px;
}

/*----------------------------------------------*/

/*---------------------------------------------
  Секция с портфолио
--------------------------------------------- */

.portfolio {
  padding: 3.125rem 0px 257px 0px;
  text-align: center;
}

.portfolio__buttonsCats {
  display: flex;
  justify-content: center;
  text-align: center;
  margin-top: 7.125rem;
  margin-bottom: 120px;
  margin-left: 25px;
}

.titleSection {
  padding-left: 3px;
  margin-bottom: 15px;
  text-align: left;
  color: #ffdb00;
  font-size: 3.125rem;
  line-height: 1.92;
  text-transform: uppercase;
  font-weight: 400;
  letter-spacing: 3.75px;
}

.descSection {
  max-width: 945px;
  text-align: left;
  color: #181a1f;
  font-size: 1.18rem;
  line-height: 1.25;
  font-weight: 400;
  letter-spacing: 1.5px;
}

.buttonCat {
  display: inline-block;
  padding: 7px 33px;
  border: 2px solid #ffda00;
  color: #181a1f;
  font-size: 20px;
  font-weight: 400;
  letter-spacing: 1.5px;
}

.buttonCat--photo {
  padding: 7px 49px;
}

.buttonCat-position {
  margin-right: 27px;
}

.buttonCat--active,
.buttonCat:hover {
  background-color: #ffda00;
}

.portfolio__content {
  display: flex;
  width: 100%;
  background-color: #17191e;
}

.portfolio__left {
  width: 26%;
}

.portfolio__rightTop {
  display: flex;
}

.portfolio__item1 {
  width: 48.6%;
  text-align: left;
  padding: 113px 90px 113px 74px;
  background-color: #ffda00;
  opacity: 0.9;
}

.portfolio__title {
  margin-top: 0;
  color: #181a1f;
  font-size: 1.85rem;
  line-height: 1;
  font-weight: 400;
  letter-spacing: 2.25px;
}

.portfolio__desc {
  color: #ffffff;
  font-size: 1.25rem;
  line-height: 1.25;
  font-weight: 400;
  letter-spacing: 1.5px;
}

.portfolio__buttonItem {
  padding: 14px 49px 15px 40px;
  border: 2px solid #ffffff;
  color: #181a1f;
  font-size: 20px;
  line-height: 6.82;
  font-weight: 400;
  letter-spacing: 1.5px;
}

.portfolio__buttonItem:hover {
  background: #ffffff;
}

.portfolio__rightDown {
  display: flex;
}

.portfolio__item3 {
  width: 35%;
  background-color: #3c3b32;
}

.portfolio__item4 {
  width: 30%;
  background-color: #5d5c50;
}

.portfolio__item5 {
  width: 35%;
  background-color: #3c3b32;
}

.portfolio__item {
  display: block;
  cursor: pointer;
}

.portfolio__item img {
  max-width: 100%;
  display: block;
  object-fit: cover;
}

.portfolio__button {
  margin-top: 60px;
  padding: 20px 102px;
  border: 2px solid #ffda00;
  border-radius: 30px;
  color: #181a1f;
  font-size: 17px;
  line-height: 8;
  text-transform: uppercase;
  font-weight: 400;
  letter-spacing: 1.26px;
}

.portfolio__button:hover {
  background: #ffda00;
}

/*----------------------------------------------*/

/*---------------------------------------------
  Секция клиенты
--------------------------------------------- */

.clients {
  padding: 50px 0 115px 0;
  background-color: #17191e;
}

.descSection--clients {
  color: #ffffff;
}

.clients__slider {
  margin-top: 120px;
}

.clients__slider .owl-dots {
  width: 100%;
  text-align: center;
  margin-top: 47px;
}

.clients__slider .owl-dots .owl-dot {
  width: 10px;
  height: 10px;
  display: inline-block;
  background-color: #ffffff;
  margin-left: 10px;
  transition: background-color 0.5s ease transform 0.5s ease;
  outline: none;
}

.clients__slider .owl-dots .owl-dot.active {
  background-color: #ffda00;
  transform: scale(1.5);
}

.clients__item img {
  width: auto !important;
}

/*----------------------------------------------*/

/*---------------------------------------------
  Секция контакты
--------------------------------------------- */

.contacts {
  padding-top: 48px;
}

.form__wrapper {
  max-width: 972px;
  margin: 120px auto;
  margin-bottom: 98px;
}

.form {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: space-between;
}

.form__name,
.form__email,
.form__subject,
.form__message,
.form__button {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(50% - 30px);
  margin-bottom: 18px;
  padding: 10px 20px 11px 24px;
  border: 2px solid #17191e;
  font-family: "AbelRegular";
  color: #181a1f;
  font-size: 20px;
  font-weight: 400;
  letter-spacing: 1.5px;
}

.form__name:focus,
.form__email:focus,
.form__subject:focus,
.form__message:focus,
.form__button:focus {
  outline: none;
}

.form__subject {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(100% - 30px);
  margin-top: 0px;
}

.form__message {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(100% - 30px);
  height: 182px;
  margin-bottom: 40px;
  resize: none;
}

.form__message::placeholder {
  font-family: "AbelRegular";
  color: #181a1f;
  font-size: 20px;
  font-weight: 400;
  letter-spacing: 1.5px;
  line-height: 1.3;
}

.form__button {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(40% - 30px);
  margin: 0 auto;
  padding: 20px 32px 22px 20px;
  text-transform: uppercase;
  background: transparent;
  border: 2px solid #ffda00;
  border-radius: 30px;
  cursor: pointer;
}

.form__button:hover {
  background: #ffda00;
}

::placeholder {
  font-family: "AbelRegular";
  color: #181a1f;
  font-size: 20px;
  line-height: 4.8;
  font-weight: 400;
  letter-spacing: 1.5px;
}

:focus::placeholder {
  color: transparent;
}

/* Чипс для формы
------------------------------------------------*/

.chips-field {
  position: fixed;
  right: 0;
  left: 0;
  top: 20%;
  max-width: 40%;
  margin: 0 auto;
}

.chips-field .chipp {
  padding: 4rem 1.5rem;
  background: rgba(0, 0, 0, 0.7);
  color: #ffda00;
  font-size: 1.4rem;
  font-weight: bold;
  text-align: center;
}

/*----------------------------------------------*/

.contactsItems__wrapper {
  padding: 112px 0;
  background-color: #ffda00;
}

.contacts__items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: space-around;
}

.contactsItem {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(26.25% - 30px);
  align-items: center;
  flex-wrap: nowrap;
}

.contactsItem__pic {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 80px;
  height: 80px;
  margin-right: 22px;
  background-color: #17191e;
}

.contactsItem__phone,
.contactsItem__email,
.contactsItem__adres {
  color: #181a1f;
  font-size: 20px;
  line-height: 4.8;
  font-weight: 400;
  letter-spacing: 1.5px;
}

.contactsItem__adres {
  line-height: 1.3;
}

.contactsItem:last-child {
  flex-wrap: nowrap;
}

/*----------------------------------------------*/

/*============================================= */

/*=============================================
        ПОДВАЛ
============================================= */

.footer {
  padding: 83px 0 20px 0;
  background-color: #17191e;
  text-align: center;
}

.footer__copy {
  color: #ffffff;
  font-size: 25px;
  line-height: 3.84;
  font-weight: 400;
  letter-spacing: 1.88px;
}

.footer__copy span {
  color: #ffdb00;
}

.footer__social {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 41px;
}

.footer__linkSocial {
  margin-left: 37px;
}

.footer__linkSocial:first-child {
  margin-left: 0;
}

.footer__buttonScroll {
  position: fixed;
  right: 40px;
  bottom: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  border: 1px solid #000;
  background: #ffda00;
  opacity: 0.5;
  cursor: pointer;
}

/*============================================= */

/*=============================================
        АДАПТИВ
============================================= */

@media screen and (max-width: 1200px) {
  .contacts__items {
    justify-content: center;
  }

  .contactsItem {
    width: calc(50% - 30px);
  }

  .contactsItem {
    margin-bottom: 20px;
  }
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .header {
    padding-bottom: 160px;
  }

  .header__title {
    margin-top: 150px;
  }

  .quote {
    padding-left: 30px;
    padding-right: 30px;
    font-size: 18px;
  }

  .indicators {
    padding-bottom: 0px;
  }

  .itemIndic {
    width: calc(50% - 30px);
  }

  .itemIndic {
    margin-bottom: 70px;
  }

  .itemIndic__number {
    margin-top: 10px;
  }

  .itemIndic__title {
    margin-top: -20px;
  }

  .portfolio__buttonsCats {
    display: block;
    margin-bottom: 40px;
  }

  .buttonCat-position {
    margin-bottom: 15px;
  }

  .portfolio__rightTop {
    flex-direction: column;
    align-items: center;
  }

  .portfolio__item1 {
    width: 70%;
    padding: 31px 70px 11px 74px;
  }

  .portfolio__rightDown {
    flex-direction: column;
    align-items: center;
  }

  .portfolio__item {
    width: 70%;
    margin-bottom: 5px;
  }
}

@media all and (max-width: 900px) {
  .mfp-arrow {
    -webkit-transform: scale(0.75);
    transform: scale(0.75);
  }

  .mfp-arrow-left {
    -webkit-transform-origin: 0;
    transform-origin: 0;
  }

  .mfp-arrow-right {
    -webkit-transform-origin: 100%;
    transform-origin: 100%;
  }

  .mfp-container {
    padding-left: 6px;
    padding-right: 6px;
  }
}

@media screen and (max-width: 800px) and (orientation: landscape), screen and (max-height: 300px) {
  /**
       * Remove all paddings around the image on small screen
       */

  .mfp-img-mobile .mfp-image-holder {
    padding-left: 0;
    padding-right: 0;
  }

  .mfp-img-mobile img.mfp-img {
    padding: 0;
  }

  .mfp-img-mobile .mfp-figure:after {
    top: 0;
    bottom: 0;
  }

  .mfp-img-mobile .mfp-figure small {
    display: inline;
    margin-left: 5px;
  }

  .mfp-img-mobile .mfp-bottom-bar {
    background: rgba(0, 0, 0, 0.6);
    bottom: 0;
    margin: 0;
    top: auto;
    padding: 3px 5px;
    position: fixed;
    box-sizing: border-box;
  }

  .mfp-img-mobile .mfp-bottom-bar:empty {
    padding: 0;
  }

  .mfp-img-mobile .mfp-counter {
    right: 5px;
    top: 3px;
  }

  .mfp-img-mobile .mfp-close {
    top: 0;
    right: 0;
    width: 35px;
    height: 35px;
    line-height: 35px;
    background: rgba(0, 0, 0, 0.6);
    position: fixed;
    text-align: center;
    padding: 0;
  }
}

@media screen and (max-width: 720px) {
  .itemAmen {
    width: calc(50% - 30px);
  }

  .itemAmen {
    text-align: center;
  }

  .itemAmen__icon {
    margin: 0 auto;
  }

  .portfolio__item1 {
    padding: 31px 15px 11px 10px;
  }

  .form__wrapper {
    margin: 30px auto;
  }

  .form {
    justify-content: center;
  }

  .form__name,
  .form__email,
  .form__subject,
  .form__message,
  .form__button {
    width: calc(100% - 30px);
  }

  .form__message {
    margin-bottom: 20px;
  }

  .form__button {
    width: calc(50% - 30px);
  }

  .form__button {
    padding: 15px 0;
  }

  .chips-field {
    max-width: 70%;
  }

  .chips-field .chipp {
    padding: 2rem 1rem;
  }

  .contactsItems__wrapper {
    padding: 40px 0;
  }
}

@media screen and (max-width: 576px) {
  .header {
    padding-top: 20px;
    padding-bottom: 80px;
  }

  .header__top {
    justify-content: space-around;
  }

  .header__burger {
    order: 2;
  }

  .visible {
    order: 3;
  }

  .visible {
    width: calc(100% - 30px);
  }

  .header__title {
    margin-top: 90px;
  }

  .quote {
    padding: 40px 15px;
    font-size: 15px;
  }

  .amenities {
    padding: 80px 0;
  }

  .amenities__button {
    padding: 25px 50px;
    font-size: 15px;
  }

  .itemAmen {
    width: calc(100% - 30px);
  }

  .itemAmen__title {
    font-size: 40px;
  }

  .itemIndic {
    width: calc(100% - 30px);
  }

  .portfolio__buttonItem {
    padding: 10px 25px 10px 25px;
    font-size: 17px;
  }

  .portfolio__button {
    padding: 20px 50px;
  }

  .contactsItem {
    width: calc(91.66667% - 30px);
  }

  .footer__copy {
    margin-top: -30px;
    font-size: 20px;
    line-height: 1.3;
  }

  html {
    font-size: 75%;
  }
}

@media screen and (max-width: 320px) {
  .itemAmen__desc {
    margin-bottom: 50px;
  }
}